/* Main container */
.container {
    display: grid;
	grid-template-areas: 
		'left-column right-column';
	grid-template-rows: minmax(385px, auto);
	grid-template-columns: 5fr 3fr;
	column-gap: 10px;
	row-gap: 10px;
	font-family: 'Josefin Sans', sans-serif;
}

.left_column {
	display: flex;
	flex-direction: column;
	grid-area: left-column;
}

/* Subcontainer for tournament and organiser info */
.info_organiser_container {
	display: grid;
	grid-template-areas: 'info organiser';
	grid-template-columns: minmax(200px, 1fr) minmax(200px, 1fr);
	width: 100%;
	column-gap: 10px;
	row-gap: 10px;
	align-items: stretch;
	justify-content: stretch;
	
}

/* TODO: Sticky table header */
.th {
	display: sticky;
}


/* Organiser */

div.organiser_container {
	margin: 0px;
	box-shadow: none;
	max-width: unset;
	justify-self: stretch;
	width: 100%;
	box-sizing: border-box;
}

/* Tiebreak and Prizes Buttons */


section.tieBreakSection {
	margin-bottom: 0px !important;
	margin-top: 0px !important;
}

.tieBreakContainer, .tieBreakContainer:hover {
	max-width: 500px;
	position: fixed;
	height: auto;
	display: flex;
	top: 19vh;
	background-color: rgb(224, 224, 224);
	transition: left 0.5s ease;
	justify-content: center;
	z-index: 2;
}

.tieBreakContainer:not(.visible) {
	left: -100%;
}

.tieBreakContainer.visible {
	left: calc((100vw - 1280px) / 2 - 10px)
}

@media only screen and (max-width: 1080px) {
	.tieBreakContainer.visible {
		left: 20px;
	}
}

@media only screen and (max-width: 700px) {
	.tieBreakContainer, .tieBreakContainer:hover {
		position: static;
		max-width: unset;
	}
	.tieBreakContainer:not(.visible) {
		display: none;
	}
	.tieBreakContainer.visible {
		display: flex;
	}
}

.tieBreakContainer > div {
	width: 100%;
}

.tieBreakTitle {
	padding: 5px;
	border-radius: 15px;
	background-color: rgba(150, 150, 150, 0.6);
	display: flex;
	justify-content: center;
	font-weight: bold;
	cursor: pointer;
}

.tieBreakTitle:hover {
	background-color: rgba(65, 65, 65, 0.6);
}

.tieBreaks {
	display: grid;
	grid-template-columns: auto 1fr;
	font-size: smaller;
	margin: 10px 0;
}

.tieBreaks > div {
	padding: 2px 5px;
}

/* Featured/Upcoming games */
.featured {
	margin-top: 0px;
	max-height: 600px;
}

.live {
	grid-area: live;
}

.teams_announcements {
	grid-area: right-column;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.teams_list {
	height: auto;
	max-height: 1200px;
	box-sizing: border-box;
}

.header {
	font-family: 'Josefin Sans', sans-serif;
	font-size: 20px;
	font-weight: bold;
	text-align: center;
	margin: 5px 0px;
}

.announcements {
	height: auto;
	max-height: 500px;
	overflow: auto;
}

/* Table of registered players */

.table {
	width: 100%;
	max-height: 555px;
	overflow: scroll;
	margin: unset;
}

.table :global(.playerName.left) {
	width: 100%;
	justify-content: flex-start;
}

.cell, a:global(.playerName) {
	font-family: Arial, Helvetica, sans-serif;
}
th.headerCell {
	display: none;
}

.flag {
	position: relative;
	width: 2.5em;
	height: 2.5em;
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	text-align: center;
}

.flag > * {
	display: block;
	padding-left: 9px;
	padding-top: 4px;
	/* TODO: Find a way to centre these flags */
}

.tr {
	height: 30px;
}

@media only screen and (max-width: 700px) {
	th.headerCell {
		display: table-cell;
	}
	th.headerCell > div {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		color: black;
	}
	table > tbody > tr.tr {
		display: grid;
		grid-template-columns: 50% 50%;
		background-color: #f0f0f0;
	}
	table td {
		text-align: center;
	}
	tr.tr > th:nth-child(4n), tr.tr > th:nth-child(4n-1), tr.tr > td:nth-child(4n) {
		background-color: lightblue;
	}
}